.navBarItems{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 80px;
    height: 80px;
    z-index: 1;
    border-bottom: 2px solid black;
}

.logo{
    text-decoration: none;
    color: black;
}

.logo img{
    width: 60px;
}

.rightNav{
    display: flex;
    align-items: center;
    justify-content: right;
}

.rightNav li{
    list-style: none;
    padding: 0 20px;
    position: relative;
    font-size: 1.3rem;
}

.nav-links{
    text-decoration: none;
    color: black;
    transition: 0.3s ease-in-out;
}


